import { resetNowIndex } from './capture';
import { addTestClass } from './collection-init-info';
import { post } from './reset-api';
import { setNowInfo } from './result-manage';
import { loadScene } from './scene-manage';
import { promisify } from './utils';

export interface ClassFunction {
    clazz: any;
    func: string;
}

export class TestClassInfo {
    private static _instance: TestClassInfo;
    static get instance() {
        if (!TestClassInfo._instance) {
            TestClassInfo._instance = new TestClassInfo();
        }
        return TestClassInfo._instance;
    }
    classMap: Map<string, Function> = new Map();
    setupFuncMap: Map<Function, string[]> = new Map();
    setupClassMap: Map<Function, string[]> = new Map();
    teardownFuncMap: Map<Function, string[]> = new Map();
    teardownClassMap: Map<Function, string[]> = new Map();
    functionMap: Map<Function, string[]> = new Map();
    testCaseList: ClassFunction[] = [];
    sceneName: Map<Function, string> = new Map();
}

type newObj = {
    new(): TestObj;
    name: string;
};
type TestObj = {
    scene?: string;
    name?: string;
    setup?(): Promise<any>;
    setupFunc?(): Promise<any>;
    teardown?(): Promise<any>;
    teardownFunc?(): Promise<any>;
    testCase?: TestCase[];
    [key: string | number]: any;
};

type TestCase = {
    (): Promise<any>,
    realName: string
}

let nowObj: TestObj | null;

const nowCaseList: string[] = [];

// const nameToMap: Map<string, newObj> = new Map()
export const testClassMap: Map<string, TestObj> = new Map();

export function testClass(classname: string) {
    return function (ctor: newObj) {
        try {
            // // @ts-ignore
            TestClassInfo.instance.classMap.set(classname, ctor);
            const testObj = getTestObj(ctor.name);
            testClassMap.delete(ctor.name);
            nowObj = testObj;
            testObj.name = classname;
            testClassMap.set(classname, testObj);
            // console.info('testClass~~~', ctor.name, classname);
        } catch (error) {
            console.error('„Äêautomation error„Äë', JSON.stringify(error));
            throw new Error('Ë£ÖÈ•∞Âô®Ëé∑ÂèñÊµãËØïÁ±ªÂêçÂ§±Ë¥•!');
        }
    };
}

export function runScene(scene: string) {
    return function (className: newObj) {
        try {
            // @ts-ignore
            TestClassInfo.instance.sceneName.set(className, scene);
            console.log('className', className);
            const obj = nowObj || getTestObj(className.name);//Âà§Êñ≠Á±ªÂêçÊòØÂê¶Â∑≤ÁªèÂàõÂª∫‰∫ÜÔºåÂàõÂª∫‰∫Ü‰∏çÂÅöÂàõÂª∫ÔºåÁõ¥Êé•ËøîÂõûÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàõÂª∫Áõ¥Êé•ÂàõÂª∫
            nowObj = null;
            obj.scene = scene;
            addTestClass({
                name: obj.name!,
                scene,
                caseList: nowCaseList.reduce((obj, name) => {
                    obj[name] = [];
                    return obj;
                }, {}),
            });
            nowCaseList.length = 0;
            // console.info('runScene~~~');
        } catch (error) {
            console.error('„Äêautomation error„Äë', JSON.stringify(error));
            throw new Error('Êú™Ëé∑ÂèñÂà∞Âú∫ÊôØÂêçÁß∞ÔºÅ');
        }
    };
}

export function setupFunc(proto: any, functionName: string) {
    try {
        TestClassInfo.instance.setupFuncMap.set(proto.constructor, [functionName]);
        getTestObj(proto.constructor.name, proto.constructor).setupFunc = promisify(proto[functionName]);
    } catch (error) {
        console.error('„Äêautomation error„Äë', JSON.stringify(error));
        throw new Error('Ë£ÖÈ•∞Âô®Ëé∑ÂèñsetupFuncÂ§±Ë¥•!');
    }
}

export function setup(proto: any, functionName: string) {
    try {
        TestClassInfo.instance.setupClassMap.set(proto.constructor, [functionName]);
        getTestObj(proto.constructor.name, proto.constructor).setup = promisify(proto[functionName]);
    } catch (error) {
        console.error('„Äêautomation error„Äë', JSON.stringify(error));
        throw new Error('Ë£ÖÈ•∞Âô®Ëé∑ÂèñsetupÂ§±Ë¥•!');
    }
}

export function teardownFunc(proto: any, functionName: string) {
    try {
        TestClassInfo.instance.teardownFuncMap.set(proto.constructor, [functionName]);
        getTestObj(proto.constructor.name, proto.constructor).teardownFunc = promisify(proto[functionName]);
    } catch (error) {
        console.error('„Äêautomation error„Äë', JSON.stringify(error));
        throw new Error('Ë£ÖÈ•∞Âô®Ëé∑ÂèñteardownFuncÂ§±Ë¥•!');
    }
}

export function teardown(proto: { constructor: newObj; }, functionName: string) {
    try {
        TestClassInfo.instance.teardownClassMap.set(proto.constructor, [functionName]);
        getTestObj(proto.constructor.name, proto.constructor).teardown = promisify(proto[functionName]);
    } catch (error) {
        console.error('„Äêautomation error„Äë', JSON.stringify(error));
        throw new Error('Ë£ÖÈ•∞Âô®Ëé∑ÂèñteardownÂ§±Ë¥•!');
    }
}

export function testCase(proto: { constructor: newObj; }, functionName: string) {
    try {
        TestClassInfo.instance.testCaseList.push({ clazz: proto.constructor, func: functionName });
        const obj = getTestObj(proto.constructor.name, proto.constructor);
        // proto[functionName].realName = functionName;
        // obj.testCase!.push(proto[functionName]); //ÊåáÂêëÂà∞windowsÔºåÊ≤°ÊúâÂÜçÂØπË±°‰∏äÔºåÂΩìÊó∂Âõ†‰∏∫ËÑöÊú¨‰∏≠ÁöÑdtÊâæ‰∏çÂà∞
        const fn = proto[functionName].bind(obj);
        fn.realName = functionName;
        obj.testCase!.push(fn);
        nowCaseList.push(functionName);
        // console.info('testCase~~~', proto.constructor.name);
    } catch (error) {
        console.error('„Äêautomation error„Äë', JSON.stringify(error));
        throw new Error('Ë£ÖÈ•∞Âô®Ëé∑ÂèñtestCaseÂ§±Ë¥•!');
    }
}

export function getTestObj(name: string, clazz?: newObj): TestObj {
    let obj = testClassMap.get(name);
    if (clazz && !obj) {
        obj = new clazz();
        obj.testCase = [];
        testClassMap.set(name, obj);
        obj.name = clazz.name;
    }
    console.info(`getTestObj ---obj,${obj} `);
    return obj!;
}

export async function runAllTestObj(jobId: string, platformIndex: number, nameList: string[], testScripts: string[]) {
    let idx = -1;
    // console.info('runAllTestObj~~~', testClassMap);
    for (const className of nameList) {
        idx += 1;
        const testObj = testClassMap.get(String(className))!;
        console.log('üöÄ ~ file: fixture.ts ~ line 177 ~ runAllTestObj ~ testObj', testObj, String(className));
        // const className = nameList[idx++];
        if (!testObj) continue;
        const { setup, setupFunc, teardown, teardownFunc, scene } = testObj;
        scene && await loadScene(scene, testScripts[idx]);
        setup && await setup();
        for (const testCase of testObj.testCase!) {
            const functionName = testCase.realName;
            setNowInfo(className, functionName, testScripts[idx]);
            resetNowIndex();
            setupFunc && await setupFunc();
            try {
                // eslint-disable-next-line no-await-in-loop
                await testCase();
            } catch (error) {
                // eslint-disable-next-line @typescript-eslint/no-floating-promises
                // post('failScript', { jobId, platformIndex, testScripts: testScripts[idx], className, testCase });
                console.error(`„Äêautomation error„ÄëjobId:${jobId},platformIndex: ${platformIndex},${testScripts[idx]},classname:${className},functionname:${functionName},error:${error}`);
            }
            teardownFunc && await teardownFunc();
        }
        teardown && await teardown();
        // testClassMap.delete(testObj.name!);
        // testClassMap.delete(className);
    }
}
